export default function Cart({ cart, updateCartItem, total, orderStatus, createOrder, checkout, cancelOrder }) {
  return (
    <div className="border p-4 rounded-lg">
      {cart.length === 0 ? (
        <p>Your cart is empty</p>
      ) : (
        <>
          {cart.map(item => (
            <div key={item.product_id} className="flex justify-between items-center mb-2">
              <span>Product {item.product_id} - ${item.unit_price} x {item.quantity}</span>
              <div>
                <button
                  onClick={() => updateCartItem(item.product_id, item.quantity - 1)}
                  className="bg-gray-300 px-2 py-1 rounded mr-1"
                >
                  -
                </button>
                <button
                  onClick={() => updateCartItem(item.product_id, item.quantity + 1)}
                  className="bg-gray-300 px-2 py-1 rounded mr-1"
                >
                  +
                </button>
                <button
                  onClick={() => updateCartItem(item.product_id, 0)}
                  className="bg-red-500 text-white px-2 py-1 rounded"
                >
                  Remove
                </button>
              </div>
            </div>
          ))}
          <div className="mt-4">
            <strong>Total: ${total}</strong>
          </div>
          
          <div className="mt-4 space-x-2">
            {!orderStatus && (
              <button
                onClick={createOrder}
                className="bg-green-500 text-white px-4 py-2 rounded hover:bg-green-600"
              >
                Create Order
              </button>
            )}
            
            {orderStatus === 'created' && (
              <button
                onClick={checkout}
                className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"
              >
                Checkout
              </button>
            )}
            
            {(orderStatus === 'created' || orderStatus === 'confirmed') && (
              <button
                onClick={cancelOrder}
                className="bg-red-500 text-white px-4 py-2 rounded hover:bg-red-600"
              >
                Cancel Order
              </button>
            )}
          </div>
          
          {orderStatus && (
            <p className="mt-2">Order Status: <strong>{orderStatus}</strong></p>
          )}
        </>
      )}
    </div>
  );
}